
.back_button {
  background-color: rgba(255,255,255,.1);
  color: rgba(255,255,255,0.8);
  border: none;
  display: inline-block;
  width: auto;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 20px;
  line-height: 36px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.back_button:hover {
background-color: rgb(251, 0, 0);
}
.icon {
  width: 36px;
}

.text {
  padding: 0;
  margin: 0;
  text-transform: capitalize;
}